<style  scoped>
header {
  background: #07111b;
}
nav {
  padding: 24px 0;
  box-shadow: 0 2px 4px 0 rgba(28, 31, 33, 0.06);
}
.search {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  overflow: hidden;
}
.search > input {
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
  outline: none;
  border: none;
  width: 736px;
  padding: 12px;
  background: #f1f1f1;
  color: #93999f;
  letter-spacing: 2px;
  font-size: 16px;
}
.search > button {
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
  width: 104px;
  outline: none;
  border: none;
  background: #f01414;
  color: white;
}
main {
  display: flex;
  justify-content: center;
  padding: 36px 0;
  background: #f2f5f4;
}
.main {
  width: 900px;
  display: flex;
  flex-wrap: wrap;
  background: white;
}
.main_contain {
  padding: 12px 18px;
  display: flex;
}
.total {
  font-size: 24px;
  padding: 12px 18px;
}
.main_l {
  font-size: 0;
  border-radius: 16px;
  overflow: hidden;
}
.main_r {
  padding: 12px 24px;
}
.main_r > h5 {
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
}
.main_r > p {
  font-size: 12px;
  color: rgb(77, 85, 93);
  font-weight: 400;
  line-height: 24px;
}
.main_r > p,
.main_r > h5 {
  margin: 12px 0;
}
.main_r > p > span {
  padding: 5px 20px;
}
.main_nav {
  width: 486px;
}
.main>div:nth-child(2){
  line-height: 66px;
  font-size: 32px
}
</style>
<template>
  <div>
    <header>
      <navs :sum="num" />
    </header>
    <nav>
      <div class="search">
        <input
          onkeyup="this.value=this.value.replace(/[, ]/g,'')"
          type="text"
          v-model="words"
          placeholder="搜索你感兴趣的"
          @keyup.enter="searchs"
        />
        <button type="button" @click="searchs">搜索</button>
      </div>
    </nav>
    <main>
      <div class="main">
        <div class="total">
          <h3>全部内容:{{total}}条</h3>
        </div>
        <div v-show="total>0" class="main_contain" v-for="(item ,index) in contain" :key="index">
          <div class="main_l">
            <img :src="item.cover_src" alt />
          </div>
          <div class="main_r">
            <h5>{{item.title}}</h5>
            <p>{{item.introduce}}</p>
            <p>
              <span>作者:{{item.author}}</span>
              <span>观看量:{{item.watch}}</span>
              <span>{{item.comment}}</span>
            </p>
          </div>
        </div>

        <div v-show="total<1">
          <h4>暂无此类数据</h4>
        </div>
      </div>

      <div class="main_nav"></div>
    </main>
  </div>
</template>
<script>
import navs from "../components/nav";

export default {
  components: {
    navs,
  },
  data() {
    return {
      num: null,
      words: this.$route.query.words,
      contain: [],
      total: 0
    }
  },
  methods: {
    search() {
      this.$axios({
        url: "http://127.0.0.1:3000/api/search",
        params: {
          words: this.words
        }
      }).then(res => {
        this.contain = res.data.data;
        this.total = res.data.total;
      })
    },
    searchs() {
      if (this.$route.query.words === this.words || this.words == "") {
        return
      }
      this.$router.push({ name: 'search', query: { words: this.words } });
      this.search();
    }
  },
  mounted() {
    this.search();
  },
}
</script>
